<template>
  <div class="submitReservation">
    <venue-head :paths='paths' title="我预约的" down="" ></venue-head>
    <ul class="reservatList">
      <li><span>申请理由</span><input type="text" v-model="applicationReason" placeholder="如：打球"></li>
      <li><span>申请人</span><input type="text" placeholder='张小情' :value="items.nickname" readonly ></li>
      <li><span>电话</span><input type="text" placeholder="请输入" :value="items.phone"></li>
    </ul>
    <div class="timeList">
      <div class="statTime">开始时间 <input type="text" placeholder="请选择" ref='ysEnd' class="ys-end"></div>
      <div class="LengthUse">使用时长 
        <select v-model="selectChageTxt" name="" class="lengthUseList">
          <option value="">请选择</option>
          <option v-for="(item, index) in timeList" :key="index" :value="item.keys">{{item.name}}</option>
        </select>
        <input v-if='selectChageTxt=="customize"' type="text" ref="customizeipt" class="customizeipt" placeholder="自定时间段">
      </div>
    </div>
    <div class="time-occupy-available">
      <div class="occupy-available">
        <span class="occupy">已占用</span>
        <span class="available">可预约</span>
      </div>
      <div class="progress_bar" ref="time_bar_content">
        <div class="time_bar_content" :style="{width: this.clientWidth+'px'}">
          <span class="timeBar" v-for="(itme, index) of timeBar" :key="index"></span>
          <template v-for="(item, index) in timeBar">
            <i class="numBar" v-for="item in periods"
              :key="item.uid"
              :class="{numBarbf:(item.sTs == index)}"
              :style="{left: (index)*16+'px',height: (item.sTs == index)?'57px':''}"
            >{{index}}</i>
          </template>
        </div>
      </div>
      <div class="occupation-period">
        <p>已占用时间段</p>
        <div ref='periodList' style="height: 265px;overflow: hidden">
          <ul>
            <li v-for="(item, index) in periods" :key="index">
              <span class="occupy">{{item.startTimes}}-{{item.deadLines}}</span>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="btnReservation"><div class="btn" @click="btnClick">预约</div></div>
  </div>
</template>
<script>
import VenueHead from './public/Head.vue'
import BScroll from 'better-scroll'
import axios from 'axios'
import $ from 'jquery'
export default {
  data () {
    return {
      timeList: [
        {name: '15分钟', keys: '0.25'},
        {name: '半小时', keys: '0.5'},
        {name: '1小时', keys: '1'},
        {name: '2小时', keys: '2'},
        {name: '4小时', keys: '4'},
        {name: '8小时', keys: '8'},
        {name: '自定义', keys: 'customize'}
      ],
      applicationReason: '',
      appBoardroomId: '',
      selectChageTxt: '',
      startTime: '',
      phone: '',
      title: '',
      paths: '',
      items: '',
      time: '',
      sTs: '',
      dLs: '',
      sTsMinute: '',
      dLsMinute: '',
      clientHeight: null,
      clientWidth: 780,
      timeBar: 48,
      periods: [],
      Width:[]
    }
  },
  components: {
    VenueHead
  },
  created () {
    const item = this.selectedschool.user
    const paths = this.$route.params.paths
    this.appBoardroomId = this.$route.params.item.id
    this.phone = item.phone
    this.items = item
    this.$nextTick ( () => {
      this.InitTabScroll ();
      this.InitScroll()
    });
  },
  // 挂载结束状态
  mounted: function () {
    $(".ys-end").jeDate({
      skinCell: "jedategreen",
      isinitVal: true,
      festival: false,
      initAddVal: [2, "hh"],
      format: 'YYYY-MM-DD'
    });
    this.occupation()
    this.$nextTick ( () => {
      this.InitTabScroll ();
      this.InitScroll();
    });
    
  },
  methods: {
    InitTabScroll () {
      if (!this.scroll) {
        this.scroll=new BScroll(this.$refs.time_bar_content, {
          click:true,
          scrollX:true,
          eventPassthrough:'vertical'
      });
      }else{
        this.scroll.refresh()
      }
    },
    InitScroll () {
       this.clientHeight = `${document.documentElement.clientHeight}`-45
        if (!this.scrolls) {
          this.scrolls=new BScroll(this.$refs.periodList, {
            scrollY:true,
            click:true
        });
       }else{
          this.scroll.refresh()
       }
    },
    occupation () {
      const that = this
      const params = {
        params: {
          startTime: this.$refs.ysEnd.value,
          detailId: this.appBoardroomId,
          sort: 'startTime,asc'
        }
      }
      axios.get('iapp/api/app/boardroom/appoint', params)
      .then(res => {
        if(res.data!=={}&&res.data){
          res.data[that.appBoardroomId].map( i => {
            i.startTimes = i.startTime.substr(11,5)
            i.deadLines = i.deadLine.substr(11,5)
            i.sTs = i.startTime.substr(11,2)
            i.dLs = i.deadLine.substr(11,2)
            i.sTsMinute = i.startTime.substr(14,2)
            i.dLsMinute = i.deadLine.substr(14,2)
          })
          that.periods = res.data[that.appBoardroomId]
          this.periods.map(i=> {
            i.Width = []
            for(var j=parseInt(i.sTs); j<= parseInt(i.dLs); j++) {
              i.Width.push(j)
            }
          })
          console.log(this.periods)
        }
      })
    },
   
    btnClick () {
      const wu = this.$Toast
      const that = this
      const params = {
        params: {
          appBoardroomDetail: this.items
        }
      }
      const text = this.selectChageTxt
      if(!this.applicationReason) {
        wu.showToast({
          title: '申请理由不能为空',
          mask: false,
          icon: 'icon-success', // icon-success | icon-error | icon-info
          duration: 1500
        });
        return
      }
      params.params.title = that.applicationReason
      if(!this.phone) {
        wu.showToast({
          title: '电话不能为空',
          mask: false,
          icon: 'icon-success', // icon-success | icon-error | icon-info
          duration: 1500
        });
        return
      }
      params.params.phone = that.phone
      this.startTime = this.$refs.ysEnd.value
      if(!this.startTime){
        wu.showToast({
          title: '开始时间不能为空',
          mask: false,
          icon: 'icon-success', // icon-success | icon-error | icon-info
          duration: 1500
        });
        return
      }
      params.params.startTime = that.startTime

      if(text == 'customize') {
        this.time = this.$refs.customizeipt.value
      }else{
        this.time = text
      }
      if(!this.time) {
        wu.showToast({
          title: '使用时长不能为空',
          mask: false,
          icon: 'icon-success', // icon-success | icon-error | icon-info
          duration: 1500
        });
        return
      }
      params.params.time = that.time
      axios.post('/iapp/api/app/boardroom/appoint/create', params)
      .then(res => {
        console.log(res,'ifasfasdf')
        wu.showToast({
          title: res.msg,
          mask: false,
          icon: 'icon-success', // icon-success | icon-error | icon-info
          duration: 1500
        });
      })
    }
  }
}
</script>
<style lang="less" scoped>
.numBar {
  // height: 57px;
  width: 16px;
  position: absolute;
  transform: scale(.7);
  bottom: -22px;
  text-align: center;
  margin-left: 7px;
}
.numBarbf::before {
  content: '';
  display: block;
  height: 44px;
  background: #fdeab9d1;
  margin-bottom: 6px;
}
.zezhao {
  display: block;
  width: 20px;
  height: 31px;
  background: #fdeab9d1;
  position: absolute;
  top: -3px
}
.reservatList,
.timeList,
.time-occupy-available {
  background: #fff;
  .progress_bar {
    // border: 1px solid #7a8894;
    width: 90%;
    padding: .375rem /* 6/16 */;
    margin:.625rem auto;
    overflow: hidden;
    position: relative;
    height: 1.1875rem /* 19/16 */;
    height: 50px;
    &::before {
      content: '';
      display: block;
      width: 100%;
      border: 1px solid #7a8894;
      height: 32px;
      box-sizing: border-box;
      position: absolute;
      top: 0px;
      left: 0px;
    }
    .time_bar_content {
      position: absolute;
      left: 0;
      top: .1875rem;
    }
    .timeBar {
      display: inline-block;
      width: 1px; 
      height: 1rem /* 16/16 */;
      border-right: 1px solid #7a8894;
      margin: .25rem .4375rem;
      &.active {
        background: #FDEAB9;
      }
      &::after {
        content: '';
        display: inline-block;
        width: 1px;
        height: .625rem;
        border-right: 1px solid #7a8894;
        margin-left: .5rem;
      }
     /*  &::before {
        content: '';
        display: inline-block;
        width: 5px;
        height: 5px;
        border-right: 1px solid #7a8894;
        margin-left: .3125rem ;
      } */
    }
  }
  
}
.available::before,
.occupy::before {
  content: '';
  display: inline-block;
  width: .8125rem;
  height: .8125rem;
  margin: 0 .375rem;
}
.occupy::before {
  background: #FDEAB9;
}
.available::before {
  background: #7A8894;
}
.submitReservation {
  background: #f5f7fa;
  height: 100%;
  .reservatList {
    padding: .625rem;
    li {
      padding: .625rem 0;
      border-bottom: 1px solid #dbdbdb;
      span {
        display: inline-block;
        width: 3.75rem /* 60/16 */;
      }
      input {
        background: #fff;
        margin-left: 1.875rem /* 30/16 */;
      }
    }
  }
  .timeList {
    padding: 0 0.625rem;
    background: #fff;
    .statTime,.LengthUse {
      border-bottom: 1px solid #dbdbdb;
      padding: .625rem 0;
      position: relative;
    }
    .customizeipt {
      text-align: right;
      position: absolute;
      right: 1.9375rem /* 31/16 */;
      top: .75rem /* 12/16 */;
      background: #fff;
    }
    .ys-end {
      width: 70%;
      background: #fff;
      text-align: left;
      text-indent: 2.0625rem;
    }
    .lengthUseList {
      width: 30%;
      float: right;
      border: none;
      outline: none;
      padding-left: 1.875rem;
    }
  }
  .time-occupy-available {
    padding: .625rem 0;
    .occupy-available {
      text-align: center;
    }
    .occupation-period {
      padding: 0 .625rem;
      p {
        margin-bottom: .9375rem;
      }
      li {
        margin: 10px 0;
      }
      .occupy::before {
        margin-left: 0;
      }
    }
  }
  .btnReservation {
    background: #fff;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    .btn {
      color: #fff;
      background: #1f95ff;
      width: 80%;
      margin: 5px auto;
      text-align: center;
      padding: 10px 0;
      border-radius: .3125rem /* 5/16 */;
    }
  }
}
</style>



